import React from 'react';

import Jumbotron from 'react-bootstrap/Jumbotron';
import Toast from 'react-bootstrap/Toast';
import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/Button';

import axios from 'axios'

import './App.css';


class Card extends React.Component {

  constructor(props) {
    super(props);
    this.state = { clue: "Click Generate to start!", answer: "", showAnswer: false };
  }

  render() {
    const {clue, answer, showAnswer} = this.state;

    return (

<div className="facade">

        <div className="card">
        <span> {clue} </span>
        </div>

        <div>
         <Button
         onClick={async ()=>{
            var response = await axios.get("https://krx9x5v1j2.execute-api.us-east-1.amazonaws.com/prod");
            this.setState((state) => {return {clue: response.data.gab.toUpperCase(), answer: response.data.answer.toUpperCase()}});
                        }}> Generate New </Button>

        <Button onClick={async ()=>{
            if (answer === "") return;
            this.setState((state) => {return {showAnswer: !state.showAnswer}});
                        }}> {showAnswer ? "Hide Answer" : "Show Answer"} </Button>
</div>
        <div className="card-answer">
         <Toast show={showAnswer && answer !== ""} onClose={async ()=>{
            this.setState((state) => {return {showAnswer: false}});
                        }}>
            <Toast.Header>
                <strong className="mr-auto">Answer</strong>
            </Toast.Header>
            <Toast.Body>{answer}</Toast.Body>
      </Toast>
      </div>
    </div>





    );
  }
};

const App = () => (
  <Container className="p-3">
    <Jumbotron>
      <h1 className="header">Mad Gab</h1>
      <Card> Click Generate to start! </Card>
    </Jumbotron>
  </Container>
);

export default App;
